@import "iboot/_variables";
@import "iboot/_mixins";
.note-list {
    @include list-unstyled;
}

.item-note {
    padding: 15px 0;
    border-bottom: 1px solid rgba(0, 0, 0, .04);
    .avatar {
        float: left;
        display: block;
        width: 40px;
        margin-right: 6px;
        img {
            @include img-fluid;
            border-radius: 3px;
        }
    }
    .wrap-img {
        height: 210px;
        margin-top: 10px;
        background-position: center center;
        @media (min-width: map-get($breakpoints, lg)) {
            border-radius: 3px;
            display: none;
            background-image: url('...') !important;
        }
    }
    .title {
        display: block;
        overflow: hidden;
        font-size: 16px;
        font-weight: bold;
        color: rgba(0, 0, 0, 1);
        line-height: 20px;
    }
    .abstract {
        margin: 10px 0;
        background-color: rgba(0, 0, 0, .02);
        color: rgba(0, 0, 0, .45);
        font-size: 12px;
        font-weight: bold;
        padding: 10px;
        border-radius: 3px;
    }
    .meta {
        @include clearfix;
        font-size: 12px;
        font-weight: bold;
        color: rgba(0, 0, 0, .45);
    }
    .source {
        float: right;
    }
}
